/* constants */
:root {
  --ctheme: #ff8c52;
  --ctheme-lite: #ffac52;
  --c333: #333;
  --c777: #777;
  --c999: #999;
  --caaa: #aaa;
  --cddd: #ddd;
  --ce2: #e2e2e2;
  --ceee: #eee;
  --cf7: #f7f7f7;
  --cfff: #fff;
  --c-ico-green: #22aa55;
  --c-ico-red: #dd5555;
  --s11: 11px;
  --s13: 13px;
  --s15: 15px;
  --s18: 18px;
  --s20: 20px;
  --s24: 24px;
}

/* colors */
.ctheme {
  color: var(--ctheme);
}
.cfff {
  color: var(--cfff);
}
.cf7 {
  color: var(--cf7);
}
.cddd {
  color: var(--cddd);
}
.caaa {
  color: var(--caaa);
}
.c999 {
  color: var(--c999);
}
.c777 {
  color: var(--c777);
}
.c333 {
  color: var(--c333);
}

/* fonts */
.s11 {
  font-size: var(--s11);
}
.s13 {
  font-size: var(--s13);
}
.s15 {
  font-size: var(--s15);
}
.s18 {
  font-size: var(--s18);
}
.s20 {
  font-size: var(--s20);
}
.s24 {
  font-size: var(--s24);
}
.fontb {
  font-weight: bold;
}
.fontn {
  font-weight: normal;
}

/* logo */
.logo-name {
  color: var(--ctheme);
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
}
.catchline {
  color: var(--ctheme);
  margin-left: 12px;
  font-size: 14px;
  font-weight: bold;
  font-style: italic;
  line-height: 50%;
}
.silent-link {
  text-decoration: none;
}
.logo-box {
  margin: 10px 20px 10px 20px;
  padding: 10px 20px 10px 20px;
}

/* containers */
.app-content {
  flex: 2;
  padding: 20px 0px;
}

/* sidebar */
.sidebar {
  width: 275px;
}
.sidebar .logo {
  padding: 20px 0 0 25px;
}
.sidebar .logo img {
  height: 40px;
}
.row-display {
  display: inline-flex;
}
.bottom-align {
  padding-top: 23px;
  padding-left: 1.5px;
}
.sidebar .nav {
  width: 250px;
  padding: 40px;
}
/* .sidebar .nav li {} */
.sidebar .nav li a {
  padding: 10px 20px;
}
.sidebar .nav li a .ico {
  margin-right: 10px;
}
/* .sidebar .nav li a .lbl {} */
.sidebar .nav li a.on {
  border-left: 2px var(--ctheme) solid;
  color: var(--ctheme);
  line-height: 1;
}
.sidebar .updated-course {
  padding: 10px;
  margin: 10px 0px;
}
.sidebar .updated-course .ico {
  background: var(--ctheme);
  padding: 2px 12px;
  margin: 0 10px 0 1px;
  border-radius: 17px;
}
.sidebar .stats {
  margin: 20px;
  padding: 30px 0px;
}
.sidebar .stats .stats-box {
  flex-direction: column;
  width: 70px;
}
.sidebar .stats .stats-box .ico {
  margin-bottom: 5px;
}
.sidebar .stats .stats-box .ico-shield {
  color: var(--c-ico-green);
}
.sidebar .stats .stats-box .ico-battery {
  color: var(--c-ico-red);
}
.sidebar .me {
  padding: 20px 40px;
}
.sidebar .me .photo {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  overflow: hidden;
  margin-right: 10px;
  background: var(--ceee);
}
.sidebar .me .photo img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

/* home page */
/* .home-page {} */
/* .home-page .section {} */
.home-page .section .title {
  margin-bottom: 20px;
}
/* .home-page .section .tutors {} */
.home-page .section .tutors .tutor {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  border: 2px var(--ctheme) solid;
  padding: 2px;
  overflow: hidden;
  margin-right: 15px;
  background: var(--ceee);
}
.home-page .section .tutors .tutor img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}
/* .home-page .section .tutors .tutor:hover {} */
.home-page .section-b {
  margin-top: 0px;
}
/* .home-page .section .courses {} */
/* .home-page .section .courses a {} */
.home-page .section .courses .block {
  width: 200px;
  height: 300px;
  background: var(--ce2);
  border-radius: 20px;
  margin-right: 15px;
}
.home-page .section .courses .block .user {
  background: rgba(32, 64, 128, 0.67);
  width: 200px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  padding: 13px;
  z-index: 1;
}
.home-page .section .courses .block .user .pic {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 2px;
  margin-right: 7px;
}
.home-page .section .courses .block .user .pic img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
/* .home-page .section .courses .block .user .meta {} */
/* .home-page .section .courses .block .user .meta .name {} */
/* .home-page .section .courses .block .user .meta .uname {} */
.home-page .section .courses .block .dura {
  top: 60px;
  left: 13px;
  background: var(--ctheme);
  line-height: 1;
  padding: 5px 10px;
  border-radius: 7px;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.33);
  z-index: 1;
}
.home-page .section .courses .block .course-title {
  bottom: 10px;
  left: 10px;
  right: 25px;
  background: rgba(0, 0, 0, 0.5);
  padding: 10px;
  border-radius: 13px;
  text-shadow: 1px 1px 1px #000;
}
.home-page .top-tutors {
  flex-wrap: wrap;
}
.home-page .section .user-block {
  margin-right: 30px;
  margin-bottom: 15px;
}
.home-page .section .user-block .pic {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 2px;
  margin-right: 7px;
}
.home-page .section .user-block .pic img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
/* .home-page .section .user-block .meta {} */
/* .home-page .section .user-block .meta .name {} */
/* .home-page .section .user-block .meta .uname {} */

/* Rightbar */
.rightbar {
  flex: 1;
  padding: 15px 0px;
}
.rightbar .title {
  margin-bottom: 20px;
}
.rightbar .search-box {
  height: 40px;
}
.rightbar .search-box .qry {
  flex: 1;
  border: 0px;
  padding: 5px;
  height: 20px;
}
.rightbar .search-box .go {
  border: 0px;
  background: var(--ctheme);
  padding: 11px 8px 11px 12px;
  border-radius: 5px;
  color: var(--cfff);
}
.rightbar .search-box .go:hover {
  background: var(--ctheme-lite);
}
.rightbar .search-form {
  padding-right: 20px
}
.rightbar .form-heading {
  color: var(--ctheme);
  display: flex;
  justify-content: space-between;
  padding-right: 30px;
}

/* input fields */
input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]) {
  border-color: var(--ctheme);
  box-shadow: none;
}

/* Components */
.select-box {
  border: 0px;
}

/* Generic */
body {
  margin: 0px;
  padding: 0px;
}
ul,
ol {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
h2 {
  margin: 5px;
  padding: 5px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  margin: 0px;
  padding: 0px;
}
.button {
  outline: none;
}
.flex {
  display: flex;
}
.aic {
  align-items: center;
}
.jcc {
  justify-content: center;
}
.fixed {
  position: fixed;
}
.abs {
  position: absolute;
}
.rel {
  position: relative;
}
.noul {
  text-decoration: none;
}
.bl {
  display: block;
}

/* Navbar */
nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--cf7);
  padding: 20px 80px 20px 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}
#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}
#navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}
#navbar li a {
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: bold;
  color: var(--c999);
  transition: 0.3s ease-in-out;
}
#navbar li a:hover,
#navbar li a.active {  color: var(--ctheme);
}
#navbar li a:hover::after,
#navbar li a.active::after {  content: '';
  width: 30%;
  height: 2px;
  background: var(--ctheme);
  position: absolute;
  bottom: -4px;
  left: 20px
}
#mobile {
  display: none;
}
#mobile i {
  color: var(--c999);
}

/* Media */
@media screen and (max-width: 769px) {
  #navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 170px;
    right: -310px;
    width: 300px;
    height: 100vh;
    background: var(--cf7);
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0.05);
    padding: 40px 0 0 10px;
    transition: 0.3s ease-in-out;
  }
  #navbar.active {
    right: 0px;
  }
  #mobile {
    display: block;
  }
  #mobile i {
    font-size: 25px;
    cursor: pointer;
  }
  #navbar li {
    margin-bottom: 25px;
  }
}